global.AllUrl = 'http://39.106.27.66/'
global.imgUrl ='https://bambohome.b0.upaiyun.com'

 
export default{
  install(Vue,options)
  {
    Vue.prototype.changeData = function () {
             if (localStorage.isflow!='ok') {

               this.$router.push({path: '/'})
               return false
             }  
    }
  }
}


global.MyLocalStorage ={         
        Cache : {             
            /** 
             * 5M 
             * json 
             *  ie7+ 
             * @param key key 
             * @param stringVal 
             * @time   60s  
             * localStorage  
             * */  
            put : function(key,stringVal,time){  
                try{  
                    if(!localStorage){return false;}  
                    if(!time || isNaN(time)){time=60;}  
                    var cacheExpireDate = (new Date()-1)+time*1000;  
                    var cacheVal = {val:stringVal,exp:cacheExpireDate};  
                    localStorage.setItem(key,JSON.stringify(cacheVal));//  
                    //console.log(key+":"+new Date(cacheExpireDate)+"");  
                }catch(e){}   
            },  
            /***/  
            get : function (key){  
                try{  
                    if(!localStorage){return false;}  
                    var cacheVal = localStorage.getItem(key);  
                    var result = JSON.parse(cacheVal);  
                    var now = new Date()-1;  
                    if(!result){return null;}//  
                    if(now>result.exp){//  
                        this.remove(key);                     
                        return "";  
                    }  
                    //console.log("get cache:"+key);  
                    return result.val;  
                }catch(e){  
                    this.remove(key);  
                    return null;  
                }  
            },/***/  
            remove : function(key){  
                if(!localStorage){return false;}  
                localStorage.removeItem(key);  
            },/***/  
            clear : function(){  
                if(!localStorage){return false;}  
                localStorage.clear();  
            }  
        }//end Cache  
}




